export function getValidationRules(name, options) {
    const { required = false, requiredMessage, minLength, maxLength, minmaxMessage, pattern, patternMessage, format, formatMessage, validator, } = options;
    const validationRules = [];
    if (required) {
        validationRules.push({
            required,
            message: requiredMessage || `必须填写${name}`,
        });
    }
    if ('minLength' in options) {
        validationRules.push({
            minLength,
            message: minmaxMessage || `${name}长度不能低于${minLength}`,
        });
    }
    if ('maxLength' in options) {
        validationRules.push({
            maxLength,
            message: minmaxMessage || `${name}长度不能超过${maxLength}`,
        });
    }
    if (pattern) {
        validationRules.push({
            pattern,
            message: patternMessage || `${name}不满足要求`,
        });
    }
    if (format) {
        validationRules.push({
            format,
            message: formatMessage || `${name}必须是${format}`,
        });
    }
    if (validator) {
        validationRules.push({
            validator,
        });
    }
    return validationRules;
}
